<template>
  <div id="home-main">
    <img
      style="width: 320px;"
      @click="_tokenCheck"
      alt="logo"
      src="@/assets/img/gunpla_list_logo_click.png">
  </div>
</template>

<script>
import { mapActions, mapState } from 'pinia'
import { usersStore } from '@/store/users'

export default {
  name: 'home-main',
  computed: {
    ...mapState(usersStore, ['userCustomInfo'])
  },
  methods: {
    ...mapActions(usersStore, ['logout']),
    _tokenCheck () {
      if (this.userCustomInfo === undefined) this.$router.push({ name: 'user-login' })
      else this.$router.push({ name: 'user-gunpla-list' })
    }
  }
}
</script>
